import { FC, useEffect, useRef, useState } from 'react';
import { ThemeProvider, RMWCProvider, Typography, Button } from 'rmwc';
import '@rmwc/theme/styles';
import '@rmwc/typography/styles';
import '@rmwc/button/styles';
import { css } from '@emotion/react';
import { theme } from '../util';

declare global {
  interface Window {
    webkitSpeechRecognition: typeof window.SpeechRecognition;
  }
}

type Result = {
  time: number;
  final: boolean;
  content: string;
};

const SpeechRecognition =
  window.SpeechRecognition || window.webkitSpeechRecognition;

const padTime = (num: number) => num.toString().padStart(2, '0');

const formatTime = (time: number) => {
  time /= 1000;
  const seconds = Math.floor(time % 60);
  const minutes = Math.floor(time / 60) % 60;
  const hours = Math.floor(time / 3600);
  if (hours) return `${hours}:${padTime(minutes)}:${padTime(seconds)}`;
  return `${minutes}:${padTime(seconds)}`;
};

const App: FC = () => {
  const [results, setResults] = useState<Result[]>([]);
  const [baseIndex, setBaseIndex] = useState<number>(0);
  const sr = useRef<SpeechRecognition>();
  const startTime = useRef<number>();
  const start = () => {
    sr.current!.start();
    startTime.current = performance.now();
  };
  useEffect(() => {
    sr.current?.stop();
    sr.current = new SpeechRecognition();
    sr.current.interimResults = true;
    sr.current.continuous = true;
    start();
  }, []);
  useEffect(() => {
    const cb = () => {
      setBaseIndex(results.length);
      sr.current!.start();
    };
    sr.current!.addEventListener('end', cb);
    return () => sr.current!.removeEventListener('end', cb);
  }, [setBaseIndex, results, sr.current]);
  useEffect(() => {
    const cb = (event: SpeechRecognitionEvent) => {
      const newResults: Result[] = [
        ...results.slice(0, baseIndex + event.resultIndex),
        ...[...event.results].slice(event.resultIndex).map((res, i) => {
          return {
            time:
              results[baseIndex + event.resultIndex + i]?.time ||
              event.timeStamp - startTime.current!,
            final: res.isFinal,
            content: res[0].transcript
          };
        })
      ];
      setResults(newResults);
    };
    sr.current!.addEventListener('result', cb);
    return () => sr.current!.removeEventListener('result', cb);
  }, [results, setResults, baseIndex, sr.current]);
  return (
    <ThemeProvider options={theme}>
      {/* Add any desired options to the RMWCProvider */}
      <RMWCProvider>
        <Typography use="body1">
          {results
            .reduce<Result[]>((arr, res) => {
              const lastResult = arr[arr.length - 1];
              if (lastResult && !res['final'] && !lastResult['final']) {
                lastResult.content += res.content;
              } else {
                arr.push({ ...res });
              }
              return arr;
            }, [])
            .map((v) => {
              const content = v.content.trim();
              return (
                <div
                  css={css`
                    color: ${v['final'] ? 'black' : 'gray'};
                  `}
                >
                  [{formatTime(v.time)}]{' '}
                  {content[0].toUpperCase() + content.slice(1)}
                </div>
              );
            })}
        </Typography>
      </RMWCProvider>
    </ThemeProvider>
  );
};

export default App;
